* {
  margin: 0%;
  padding: 0%;

  font-family: Rajdhani, sans-serif;
}
.home {
  position: absolute;
  height: 90h;
  top: 10vh;
  display: flex;
  width: 100%;
  flex-direction: row;
}
.home-left {
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 90vh;
  width: 240px;
  color: #fff;

  background-image: url(https://affiliateindians.com/ai/members/assets/banner_bg/background.jpg);
}
.home-left2 {
  display: none;
}

.dashboard,
.user,
.leaads,
.memb,
.admin-ewallet,
.logout {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  padding: 15px 20px;
}
.dashboard {
  margin-top: 30px;
}
.advisors-inner,
.schedule-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: left;
  
}
.advisors-inner1,
.schedule-inner1 {
  display: none;
}
.view-advisors,
.add-advisors,
.schedule-book,
.schedule-call,
.my-booking {
  color: #dcb52c;
  font-size: 14px;
  padding: 10px;
}
.schedule-book {
  margin-top: 5px;
}
.advisors-outer,
.schedule-outer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
}
.dashboard:hover .dashboard-text,.dashboard:hover .dashboard-icon,
.leaads:hover .leaads-text,.leaads:hover .dashboard-icon,
.user:hover .user-text,.user:hover .dashboard-icon,
.memb:hover .memb-text,.memb:hover .dashboard-icon,
.advisors:hover .advisors-text,.advisors:hover .link-icon,.advisors:hover .key-icon,
.schedule:hover .schedule-text,.schedule:hover .link-icon,.schedule:hover .key-icon1,
.admin-ewallet:hover .admin-ewallet-text,.admin-ewallet:hover .dashboard-icon,
